body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, Helvetica, sans-serif; }

a {
  text-decoration: none; }

.light-theme {
  background-color: #ffffff;
  color: #000000; }
  .light-theme .branding {
    text-align: center;
    margin: 0px auto 20px;
    box-shadow: 0 0 5px #e3e3e3;
    padding: 8px 0; }
  .light-theme .theme {
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px 20px; }
    .light-theme .theme select {
      padding: 8px 15px;
      border-radius: 3px;
      border-width: 2px;
      background-color: #ff1d8e;
      color: #fff;
      font-weight: bold;
      outline: none;
      border: none; }
  .light-theme a {
    color: #000000;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease; }
    .light-theme a:hover {
      color: #dd0074; }
  .light-theme table.movie-list {
    margin: 0 auto;
    width: 90%;
    text-align: center;
    color: #000000; }
    .light-theme table.movie-list select {
      display: block;
      margin: 8px auto; }
    .light-theme table.movie-list thead {
      position: -webkit-sticky;
      position: sticky;
      top: 0; }
      .light-theme table.movie-list thead tr th {
        border-bottom: 5px solid #e3e3e3;
        padding: 10px 0; }
        .light-theme table.movie-list thead tr th select {
          display: inline;
          max-width: 90%;
          margin: 5px auto;
          padding: 2px;
          border-top-left-radius: 3px;
          border-bottom-left-radius: 3px;
          background-color: #ffff99;
          outline: none;
          border: none;
          text-align: left; }
        .light-theme table.movie-list thead tr th .sort {
          display: inline-block;
          border-top-right-radius: 3px;
          border-bottom-right-radius: 3px;
          background-color: #ffff99;
          padding: 0 5px 2px;
          border-left: 2px solid #e3e3e3;
          cursor: pointer; }
          .light-theme table.movie-list thead tr th .sort i {
            vertical-align: baseline; }
    .light-theme table.movie-list tr td {
      border-bottom: 1px solid #e3e3e3;
      max-width: 150px;
      min-width: 60px;
      padding: 5px 5px;
      font-size: 14px;
      word-wrap: break-word; }
      .light-theme table.movie-list tr td.title {
        max-width: 360px;
        text-align: left; }
        .light-theme table.movie-list tr td.title a:hover i {
          margin-left: 5px;
          -webkit-transition: margin-left 0.2s linear;
          transition: margin-left 0.2s linear; }
      .light-theme table.movie-list tr td.genres ul {
        list-style-type: none;
        display: inline-block; }
        .light-theme table.movie-list tr td.genres ul li {
          padding: 1px 4px;
          background-color: #000000;
          color: #ffffff;
          margin: 1px;
          border-radius: 3px;
          font-size: 12px;
          display: inline-block;
          cursor: pointer; }
  .light-theme .pagination {
    margin: 20px auto;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    width: 100%; }
    .light-theme .pagination ul {
      list-style-type: none;
      display: flex;
      justify-content: flex-end;
      width: 90%;
      margin: 0 auto; }
      .light-theme .pagination ul li {
        box-shadow: 0 1px 3px #e3e3e3;
        margin: 5px 5px;
        color: #000000;
        font-size: 30px;
        padding: 2px 30px;
        border-radius: 3px;
        background-color: #000000; }
        .light-theme .pagination ul li i {
          cursor: pointer;
          color: #ffffff;
          z-index: 100; }
  .light-theme footer {
    width: 30%;
    margin: 10px auto;
    text-align: center;
    font-size: 18px; }

